<template>
  <div class="posts">
    <div class="lgd">
        Your Posts
    </div>
    <div class="personal">
      <div v-for="post in posts" :key="post.id" class="mycard">
        <img
          :src="post.path"
          alt=""
          class="picture"
          style="width: 68px; height: 75px"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { getUserPosts } from "@/network/api.js";

export default {
  name: "Mypost",
  data() {
    return {
      posts: [],
      from: 0,
      to: 12,
    };
  },
  async created() {
    let result = await getUserPosts(
      this.$store.state.username,
      this.from,
      this.to
    );
    console.log(result)
    this.posts = result;
  },
};
</script>
