<template>
  <div class="all">
    <input type="file" ref="gg" v-show="false" @change="changeImage" />
    <div class="pic">
      <img
        :src="image"
        alt="user picture"
        @click="changeMidImage"
        style="width: 50px; height: 50px"
      />
    </div>
    <div class="name">
      {{ username }}
    </div>
    <div class="connection">
      <ul>
        <li>
          <h4>Blocked</h4>
          <h6>{{ block_len }}</h6>
        </li>
        <li>
          <h4>Follower</h4>
          <h6>{{ fans_len }}</h6>
        </li>
        <li>
          <h4>Subscriber</h4>
          <h6>{{ target_len }}</h6>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { getUser, setImage } from "@/network/api.js";
import { checkfile } from "@/assets/methods/checkext.js";

export default {
  name: "detail",
  data() {
    return {
      username: "",
      fans_len: "",
      target_len: "",
      block_len: "",
      image: "",
    };
  },
  methods: {
    async assign(token) {
      try {
        let result = await getUser(token);
        this.fans_len = result.following.length;
        this.target_len = result.followers.length;
        this.image = result.display;
        this.username = result.username;
        this.block_len = result.blocked.length;
        this.$store.state.loadshow = false
      } catch (error) {
        console.log(error);
      }
    },

    changeMidImage() {
      this.$refs.gg.click();
    },

    changeImage(e) {
      const files = e.target.files || e.dataTransfer.files;
      if (!files.length) {
        return;
      }
      let file = [...files][0];
      if (checkfile(file)) {
        this.file = file;
        this.serverImage();
      } else {
        console.log("error file type (1MB jpg, png)");
      }
    },

    async serverImage() {
      const formdata = new FormData();
      formdata.append("display", this.file);
      try {
        const result = await setImage(formdata, this.$store.state.token);
        const image = new Image();
        const reader = new FileReader();
        reader.onload = (e) => {
          this.image = e.target.result;
        };
        reader.readAsDataURL(this.file);
      } catch (error) {
        console.log("HTTP set image error", error);
      }
    },
  },
  created() {
    this.assign(this.$store.state.token);
  },
};
</script>
<style scoped>
.all {
  position: relative;
  width: 100%;
  height: 85px;
  border-bottom: 0.5px solid lightgray;
}

.pic {
  display: inline-block;
  position: relative;
  left: 20px;
  top: 12px;
  width: 50px;
  height: 50px;
  overflow: hidden;
  border-radius: 50%;
  cursor: pointer;
}

.name {
  position: relative;
  left: 20px;
  top: 10px;
}

.connection {
  margin: 0;
  padding: 0;
  /* display: block; */
  display: inline-block;
  position: absolute;
  top: 20px;
  left: 90px;
}

ul {
  margin: 3px;
  padding: 0;
}

li {
  float: left;
  list-style: none;
  margin-left: 8px;
  cursor: pointer;
}

h4 {
  font-size: 14px;
  margin: 0;
  padding: 0;
}

h6 {
  font-size: 14px;
  margin: 7px 0 0 0;
  padding: 0;
  text-align: center;
}
</style>